<template>
    <tr class="privacy-flag" :class="{'not-scrolled':!pargraph.scrolled && flag.mandatory}">
        <td class="privacy-flag-checkbox"><input
                style="margin: 0px 3px 3px 0px; " type="checkbox"
                v-model="flag.selected"
                :class="{'privacy-flag-chk-big': !greaterThan600}"
                :disabled="!pargraph.scrolled && flag.mandatory">
        </td>
        <td>
            <b><span v-html="flag.text"></span>
                <span v-if="flag.mandatory">*</span>
            </b>
        </td>
    </tr>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  data () {
    return {
      greaterThan600: window.innerWidth > 600
    }
  },
  name: 'PrivacyFlag',
  props: ['flag', 'pargraph'],
  watch: {
    'flag.selected' () {
      let $el = document.getElementsByClassName(this.flag.code)

      if ($el.length > 0) {
        for (let i = 0; i < $el.length; i++) {
          $el[i].checked = this.flag.selected
        }

        // console.log(this.flag.code, $el.checked)
      }
    }
  },
  computed: {
    ...mapGetters(['allScrolled'])
  }
}
</script>

<style lang="scss">
    .privacy-flag{
        text-align: left;
        font-size:11px;
        td {
            vertical-align: center;
        }
        .privacy-flag-checkbox {
            width:20px;
        }
    }
    .privacy-flag-chk-big {
       font-size: 40px;
    }
    .privacy-flag.not-scrolled {
        color: silver;
    }
</style>



